import React from "react";
import { ClientSettingsType } from "../../../types/types";

export const HaveChosenInfo = ({ lientSettings }: ClientSettingsType) => (
  <>
    <h3 className="chosenParam">You have chosen:</h3>
    <div className="leftMenuHomePage">
      {lientSettings.dietSelector.status ? (
        <div>
          <p>Diet:{lientSettings.dietSelector.diet}</p>
          <hr />
        </div>
      ) : (
        ""
      )}
      {lientSettings.mealTypesSelector.status ? (
        <div>
          <p>Meal type:{lientSettings.dietSelector.diet}</p>
          <hr />
        </div>
      ) : (
        ""
      )}
      {lientSettings.cuisinesList.length > 0 ? (
        <div>
          <p>Cuisines:</p>
          {lientSettings.cuisinesList.map((cuisine, index, array) => (
            <li key={index}>
              {cuisine}
              {index === array.length - 1 ? <hr /> : ""}
            </li>
          ))}
        </div>
      ) : (
        ""
      )}
      {lientSettings.intolerancesList.length > 0 ? (
        <div>
          <p>List of intolerances:</p>
          {lientSettings.intolerancesList.map((intolerance, index, array) => (
            <li key={index}>
              {intolerance}
              {index === array.length - 1 ? <hr /> : ""}
            </li>
          ))}
        </div>
      ) : (
        ""
      )}
      {lientSettings.ingridientsSelector.status ? (
        <div>
          <p>List of ingridients:</p>
          {lientSettings.ingridientsSelector.ingridients.map(
            (ingridient, index, array) => (
              <li key={index}>
                {ingridient}
                {index === array.length - 1 ? <hr /> : ""}
              </li>
            )
          )}
        </div>
      ) : (
        ""
      )}
      {lientSettings.excludeIngridientsSelector.status ? (
        <div>
          <p>List of excludes:</p>
          {lientSettings.excludeIngridientsSelector.excludeIngridients.map(
            (exclude, index, array) => (
              <li key={index}>
                {exclude}
                {index === array.length - 1 ? <hr /> : ""}
              </li>
            )
          )}
        </div>
      ) : (
        ""
      )}
    </div>
  </>
);
