import React, { createContext, useState } from "react";
import {
  ChildrenType,
  HistoryFavouriteTypes,
  SetFavouriteType,
  SetHistoryType,
  SetSettingType,
  SettingType,
} from "../types/types";
import { defaultSettings } from "../utils/defaultSettings";
import { FBInterface } from "../firebase_init/FBInterface";
import { useAuth } from "../hooks/useAuth";

type ProviderPropsType = {
  lientSettings: SettingType;
  setClientSettings?: SetSettingType;
  lientHistory: HistoryFavouriteTypes;
  setClientHistory?: SetHistoryType;
  lientFavourite: HistoryFavouriteTypes;
  setClientFavourite?: SetFavouriteType;
  setHistory?: SetHistoryType;
  setFavourite?: SetFavouriteType;
};

export const ClientSettingsContext = createContext<ProviderPropsType>({
  lientSettings: defaultSettings,
  lientHistory: {},
  lientFavourite: {},
});

export const ClientSettingsProvider = ({ children }: ChildrenType) => {
  const newCrud = new FBInterface();
  const userAuth = useAuth().user;

  const [lientSettings, setClientSettings] = useState(defaultSettings);
  const [lientHistory, setHistory] = useState({} as HistoryFavouriteTypes);
  const [lientFavourite, setFavourite] = useState({} as HistoryFavouriteTypes);

  const setClientHistory = (newHistory: HistoryFavouriteTypes) => {
    if (userAuth)
      newCrud
        .updateUserParam(userAuth, "history", newHistory)
        .then(() => {
          setHistory(newHistory);
        })
        .catch((e: Error) => {
          setHistory(newHistory);
          console.log(e);
        });
  };

  const setClientFavourite = (newFavourite: HistoryFavouriteTypes) => {
    if (userAuth)
      newCrud
        .updateUserParam(userAuth, "favourite", newFavourite)
        .then(() => {
          setFavourite(newFavourite);
        })
        .catch((e: Error) => {
          setFavourite(newFavourite);
          console.log(e);
        });
  };

  const value = {
    lientSettings,
    setClientSettings,
    lientHistory,
    setClientHistory,
    lientFavourite,
    setClientFavourite,
    setHistory,
    setFavourite,
  };

  return (
    <ClientSettingsContext.Provider value={value as ProviderPropsType}>
      {children}
    </ClientSettingsContext.Provider>
  );
};
