import {
  DietResponsType,
  SetFavouriteType,
  RecipeType,
} from "../../../types/types";

export type FafouriteParamsType = {
  lientFavourite: DietResponsType;
  setClientFavourite: SetFavouriteType | undefined;
  recipe: RecipeType["recipe"];
  isItInFafouritList: boolean;
  setIsItInFafouritList: React.Dispatch<React.SetStateAction<boolean>>;
};

export function changeFavouriteStatus({
  lientFavourite,
  setClientFavourite,
  recipe,
  isItInFafouritList,
  setIsItInFafouritList,
}: FafouriteParamsType) {
  if (isItInFafouritList) {
    delete lientFavourite[recipe.id];
    if (setClientFavourite) setClientFavourite(lientFavourite);
    setIsItInFafouritList(!isItInFafouritList);
  } else {
    const newFavourite = {
      ...lientFavourite,
      [recipe.id]: {
        id: recipe.id,
        image: recipe.image,
        title: recipe.title,
      },
    };
    if (setClientFavourite) setClientFavourite(newFavourite);
    setIsItInFafouritList(!isItInFafouritList);
  }
}
